"use client"

import { useMemo, useState } from "react"
import Link from "next/link"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { KpiStatusBadge, CriticalityBadge } from "@/components/status-badge"
import { Clock, Filter, TrendingUp } from "lucide-react"
import {
  kpiDefinitions,
  kpiExecutions,
  getKpiById,
  getControlById,
  getPendingReviews,
  getOverdueActionPlans,
} from "@/lib/mock-data"
import { DashboardChart } from "./dashboard-chart"

export function DashboardContent() {
  // filtros locais (sem depender do AppShell)
  const [period, setPeriod] = useState("2025-12")
  const [framework, setFramework] = useState("SOX")

  // Execues do perodo (pegando somente a ltima verso por KPI no perodo)
  const periodExecutions = useMemo(() => {
    const inPeriod = kpiExecutions.filter((e) => e.period === period)

    // pega a ltima verso por (kpiId + period)
    const latestByKpi = new Map<string, number>()
    for (const e of inPeriod) {
      const key = `${e.kpiId}__${e.period}`
      const current = latestByKpi.get(key) ?? 0
      if (e.version > current) latestByKpi.set(key, e.version)
    }

    return inPeriod.filter((e) => {
      const key = `${e.kpiId}__${e.period}`
      return e.version === (latestByKpi.get(key) ?? e.version)
    })
  }, [period])

  const greenCount = periodExecutions.filter((e) => e.status === "Green").length
  const yellowCount = periodExecutions.filter((e) => e.status === "Yellow").length
  const redCount = periodExecutions.filter((e) => e.status === "Red").length
  const total = greenCount + yellowCount + redCount

  const greenPct = total > 0 ? Math.round((greenCount / total) * 100) : 0
  const yellowPct = total > 0 ? Math.round((yellowCount / total) * 100) : 0
  const redPct = total > 0 ? Math.round((redCount / total) * 100) : 0

  // KPIs mensais no executados
  const monthlyKpis = kpiDefinitions.filter((k) => k.frequency === "Mensal")
  const executedKpiIds = new Set(periodExecutions.map((e) => e.kpiId))
  const missingKpis = monthlyKpis.filter((k) => !executedKpiIds.has(k.id))

  const pendingReviews = getPendingReviews()
  const overdueActions = getOverdueActionPlans()

  // Itens de ateno (Red/Yellow)
  const attentionItems = useMemo(() => {
    return periodExecutions
      .filter((e) => e.status === "Red" || e.status === "Yellow")
      .map((e) => {
        const kpi = getKpiById(e.kpiId)
        const control = getControlById(e.controlId)
        return { ...e, kpi, control }
      })
  }, [periodExecutions])

  return (
    <div className="space-y-6">
      {/* Faixa de filtros (somente contedo do dashboard) */}
      <Card>
        <CardContent className="p-4">
          <div className="flex flex-col gap-3 lg:flex-row lg:items-center lg:justify-between">
            <p className="text-sm text-muted-foreground">
              Ajuste <span className="font-medium text-foreground">framework</span> e{" "}
              <span className="font-medium text-foreground">perodo</span> para consolidar os KPIs.
            </p>

            <div className="flex flex-wrap items-center gap-3">
              <Select value={framework} onValueChange={setFramework}>
                <SelectTrigger className="h-10 w-40">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="SOX">SOX</SelectItem>
                  <SelectItem value="SOC">SOC</SelectItem>
                  <SelectItem value="ISO27001">ISO 27001</SelectItem>
                  <SelectItem value="PCI">PCI</SelectItem>
                </SelectContent>
              </Select>

              <Select value={period} onValueChange={setPeriod}>
                <SelectTrigger className="h-10 w-44">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="2025-12">Dez 2025</SelectItem>
                  <SelectItem value="2025-11">Nov 2025</SelectItem>
                  <SelectItem value="2025-10">Out 2025</SelectItem>
                  <SelectItem value="2025-09">Set 2025</SelectItem>
                  <SelectItem value="2025-08">Ago 2025</SelectItem>
                  <SelectItem value="2025-07">Jul 2025</SelectItem>
                </SelectContent>
              </Select>

              <Button variant="outline" className="h-10 gap-2 bg-transparent">
                <Filter className="h-4 w-4" />
                Mais filtros
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Summary Cards */}
      <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
        <Card className="border border-emerald-200 bg-emerald-50/50">
          <CardContent className="p-5">
            <div className="space-y-1">
              <p className="text-sm font-medium text-emerald-700">Green</p>
              <p className="text-3xl font-bold text-emerald-800">{greenPct}%</p>
              <p className="text-xs text-emerald-700/70">
                {greenCount} de {total} KPIs
              </p>
            </div>
          </CardContent>
        </Card>

        <Card className="border border-amber-200 bg-amber-50/50">
          <CardContent className="p-5">
            <div className="space-y-1">
              <p className="text-sm font-medium text-amber-700">Yellow</p>
              <p className="text-3xl font-bold text-amber-800">{yellowPct}%</p>
              <p className="text-xs text-amber-700/70">
                {yellowCount} de {total} KPIs
              </p>
            </div>
          </CardContent>
        </Card>

        <Card className="border border-red-200 bg-red-50/50">
          <CardContent className="p-5">
            <div className="space-y-1">
              <p className="text-sm font-medium text-red-700">Red</p>
              <p className="text-3xl font-bold text-red-800">{redPct}%</p>
              <p className="text-xs text-red-700/70">
                {redCount} de {total} KPIs
              </p>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-5">
            <div className="flex items-start justify-between">
              <div className="space-y-3">
                <p className="text-sm font-medium text-muted-foreground">Pendncias</p>

                <div className="space-y-1.5 text-sm">
                  <div className="flex items-center justify-between gap-6">
                    <span className="text-muted-foreground">KPIs no lanados</span>
                    <span className="font-semibold text-foreground">{missingKpis.length}</span>
                  </div>
                  <div className="flex items-center justify-between gap-6">
                    <span className="text-muted-foreground">Revises pendentes</span>
                    <span className="font-semibold text-primary">{pendingReviews.length}</span>
                  </div>
                  <div className="flex items-center justify-between gap-6">
                    <span className="text-muted-foreground">Planos atrasados</span>
                    <span className="font-semibold text-red-600">{overdueActions.length}</span>
                  </div>
                </div>
              </div>

              <Clock className="h-5 w-5 text-muted-foreground" />
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Chart */}
      <Card>
        <CardHeader className="pb-2">
          <div className="flex items-center justify-between">
            <CardTitle className="text-base font-semibold">Tendncia  ltimos 6 perodos</CardTitle>
            <TrendingUp className="h-4 w-4 text-muted-foreground" />
          </div>
        </CardHeader>
        <CardContent>
          <DashboardChart />
        </CardContent>
      </Card>

      {/* Attention Table */}
      <Card>
        <CardHeader className="pb-2">
          <div className="flex items-center justify-between">
            <CardTitle className="text-base font-semibold">Ateno imediata</CardTitle>
            <Badge variant="secondary" className="text-xs">
              {attentionItems.length} itens
            </Badge>
          </div>
        </CardHeader>
        <CardContent className="pt-0">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Controle</TableHead>
                <TableHead>KPI</TableHead>
                <TableHead>Status</TableHead>
                <TableHead>Criticidade</TableHead>
                <TableHead>Responsvel</TableHead>
                <TableHead>Valor</TableHead>
                <TableHead className="text-right">Ao</TableHead>
              </TableRow>
            </TableHeader>

            <TableBody>
              {attentionItems.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={7} className="py-10 text-center text-sm text-muted-foreground">
                    Nenhum KPI em ateno para o perodo selecionado.
                  </TableCell>
                </TableRow>
              ) : (
                attentionItems.map((item) => (
                  <TableRow key={item.id}>
                    <TableCell>
                      <div className="flex flex-col">
                        <span className="text-xs text-muted-foreground">{item.control?.code}</span>
                        <span className="text-sm font-medium text-foreground">{item.control?.name}</span>
                      </div>
                    </TableCell>

                    <TableCell className="text-sm text-foreground">{item.kpi?.name}</TableCell>

                    <TableCell>
                      <KpiStatusBadge status={item.status} />
                    </TableCell>

                    <TableCell>{item.kpi && <CriticalityBadge criticality={item.kpi.criticality} />}</TableCell>

                    <TableCell className="text-sm text-muted-foreground">{item.control?.pontoFocal.name}</TableCell>

                    <TableCell className="text-sm font-medium text-foreground">
                      {String(item.value)}
                      {item.kpi?.type === "numeric" ? "%" : ""}
                    </TableCell>

                    <TableCell className="text-right">
                      <Button variant="ghost" size="sm" asChild>
                        <Link href={`/controls/${item.controlId}`}>Ver detalhe</Link>
                      </Button>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  )
}
